import React from 'react'

import {Image} from 'react-bootstrap'
import './Song.css'
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

class Song extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            isOpen: false
        }
    }

    popImage = () => {
        this.props.songIsOpen();
        this.setState({isOpen: !this.state.isOpen})
    }

    render() {
        const {isOpen} = this.state;
        // console.log("heuy " + this.props.title)
        const image = <div className="imageModal text-center">
            <Image className="imageModal-content" src={this.props.songImg} fluid thumbnail rounded/>
        </div>

        return(
            <li>
                <p className="song-title" onClick={this.popImage}>{this.props.title}</p>
                {/* {isOpen && image} */}

                {isOpen && (
                    <Lightbox
                        mainSrc={this.props.songImg}
                        onCloseRequest={() => this.setState({ isOpen: false })}
                    />
                )}
            </li>
        );
    }
}

export default Song;